<template>
  <div id="viewer"></div>
</template>
<script>
import { Viewer } from 'photo-sphere-viewer'
import 'photo-sphere-viewer/dist/photo-sphere-viewer.css'
export default {
  data () {
    return {
      img: require('./assets/avater.pic.jpg'),
    }
  },
  mounted () {
    // 
    this.initPhotoSphere()
  },
  methods: {
    initPhotoSphere () {
      var viewer = new Viewer({
        container: document.querySelector('#viewer'),
        panorama: this.img,
        size: {
          width: '100vw',
          height: '80vh',
        },
      });
      viewer.refreshUi()
    }
  }
}
</script>